body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.keyWrapper {
	margin-left: .2em;
	margin-right: .2em;
}
label.keyLabel {
	font-size: 0.9em;
	border-radius: 50%;
	background-image: -webkit-radial-gradient(at 65% 25%, #00c0ff 0%, #0085b1 80%);
	background-image: radial-gradient(at 65% 25%, #00c0ff 0%, #0085b1 80%);
	display: inline-block;
	cursor: pointer;
	line-height: 30px;
	color: #1d1d1d;
	height: calc(2em + 1vw);
	width: calc(2em + 1vw);
}
input:checked + label.keyLabel {
	background-image: -webkit-radial-gradient(#00c0ff 0%, #e5dede 92%);
	background-image: radial-gradient(#00c0ff 0%, #e5dede 92%);
}

input {
	position: absolute;
  left: -1000px;
}

.keyContainerWrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 1 1;
	        flex: 1 1;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	margin-right: 2em;
}
.sharpFlatContainer {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
}
@media only screen and (min-device-width: 878px) {
	/* .keyContainerWrapper {
		justify-content: center;
	} */
}
.notationWrapper {
	margin: 1em;
	width: 140px;
	height: 30px;
}

label.notationLabel {
	background-image: -webkit-linear-gradient(top, #00c0ff 0%, #0085b1 80%);
	background-image: linear-gradient(to bottom, #00c0ff 0%, #0085b1 80%);
	display: inline-block;
	cursor: pointer;
	border-radius: 25px;
	width: calc(6em + 4vw);
	line-height: calc(1em + 1vw);
	height: calc(1em + 1vw);
}

input:checked + label.notationLabel {
	background-image: -webkit-radial-gradient(#00c0ff 0%, #e5dede 127%);
	background-image: radial-gradient(#00c0ff 0%, #e5dede 127%);
}

.notationContainerWrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	margin-left: 1em;
	margin-right: 1em;
}
.chartWrapper {
	position: relative;
	display: inline;
}

img {
	width: 70vw;
	border: 2px solid dimgrey;
}

.overlayImage {
	position: absolute;
	bottom: 4px;
	right: 0;
}
body {
  background-image: -webkit-linear-gradient(#101010, #283134);
  background-image: linear-gradient(#101010, #283134);
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 100vh;
  text-align: center;
}
.App-header {
  color: whitesmoke;
  text-shadow: 6px 4px 4px black;
  font-size: calc(10px + 2vmin);
}
.mainContent {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 80vw;
  padding-top: 3vh;
}
.selection {
  cursor: pointer;
  font-weight: bold;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
}

input:focus {
  outline: none;
}

